* {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  height: 100vh;
  background-image: url(https://static.vecteezy.com/system/resources/previews/014/661/403/non_2x/game-level-background-magic-road-and-fantasy-landscape-in-cartoon-style-road-green-field-and-forest-illustration-vector.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto;
}
.container .header {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.container .header h1 {
  font-size: 36px;
}
.container .header img {
  position: relative;
  width: 100px;
  height: 100px;
}
.container .main {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 30px;
  gap: 50px;
}
.container .main .char {
  height: 350px;
  width: 350px;
}
.container .main .values-money {
  display: flex;
}
.container .main .values-money p {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 26px;
  font-weight: 600;
}
.container .main .values-money .diamond {
  width: 25px;
  height: 25px;
}
.container .main .upgrade-clicks {
  display: flex;
  gap: 15px;
}
.container .main .upgrade-clicks button {
  width: 150px;
  height: 60px;
  background-color: white;
}

.preloader {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  background-color: white;
}
.preloader img {
  width: 200px;
  height: 200px;
}/*# sourceMappingURL=App.css.map */